@font-face {
  font-family: calculatorFont;
  src: url("MesloLGS_NF_Italic.b319002f.ttf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e4e7f4;
}

.container {
  width: 100vw;
  height: 100vh;
  padding: min(1.6vw, 3.2vh) 30%;
}

.container .calculator {
  background-color: #857f7f;
  border: 5px solid #000;
  border-radius: min(1vw, 1vh);
  width: 100%;
  height: 100%;
  padding: min(1.75vw, 3.5vh);
}

.container .calculator .screen-container {
  width: 100%;
  height: 16.6667%;
  padding-bottom: min(1.75vw, 3.5vh);
}

.container .calculator .screen-container button {
  text-align: right;
  border: 5px solid #000;
  border-radius: min(.8vw, .8vh);
  width: 100%;
  height: 100%;
  padding: 0 min(.8vw, 1.6vh);
  font-family: calculatorFont;
  font-size: min(4.5vw, 5vh);
  font-weight: bolder;
}

.container .calculator .buttons {
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(5, 1fr);
  gap: min(1.75vw, 3.5vh);
  width: 100%;
  height: 83.3333%;
  padding-top: min(1.75vw, 3.5vh);
  display: grid;
}

.container .calculator .buttons button {
  border: 5px solid #000;
  border-radius: min(.8vw, .8vh);
  font-size: min(2.5vw, 5vh);
}

.container .calculator .buttons .purple-background {
  background-color: #c7d2ff;
}

.container .calculator .buttons .green-background {
  background-color: #7bf756;
}

.container .calculator .buttons .button-clear {
  background-color: orange;
  border-color: red;
}

.container .calculator .buttons .button-clear:hover {
  cursor: pointer;
  background-color: #ef3939;
}

.container .calculator .buttons .change-on-hover:hover {
  cursor: pointer;
  background-color: #fee48f;
  border-color: #b18907;
}

