@font-face {
  font-family: calculatorFont;
  src: url(./MesloLGS_NF_Italic.ttf);
}

$button-height: calc(100% / 6);
$button-font-size-height: 5vh;
$button-font-size-width: 2.5vw;
$screen-font-size-height: 5vh;
$screen-font-size-width: 4.5vw;
$purple-background-color: #c7d2ff;
$green-background-color: rgb(123, 247, 86);
$button-color-hover-background: #fee48f;
$button-color-hover-border: #b18907;
$button-clear-background-color: orange;
$button-clear-border-color: red;
$button-clear-hover-color: rgb(239, 57, 57);
$button-gap-height: 3.5vh;
$button-gap-width: 1.75vw;
$calculator-radius-height: /* 25px */ 1vh;
$calculator-radius-width: /* 25px */ 1vw;
$button-radius-height: /* 10px */ 0.8vh;
$button-radius-width: /* 10px */ 0.8vw;
$screen-button-padding-height: /* 10px */ 1.6vh;
$screen-button-padding-width: /* 10px */ 0.8vw;
$container-padding-top-bottom-height: /* 20px */ 3.2vh;
$container-padding-top-bottom-width: /* 20px */ 1.6vw;
$border: 5px solid black;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #e4e7f4;
}

.container {
  width: 100vw;
  height: 100vh;
  padding: /* 20px */ min(
      $container-padding-top-bottom-width,
      $container-padding-top-bottom-height
    )
    30%;

  .calculator {
    width: 100%;
    height: 100%;
    background-color: rgb(133, 127, 127);
    border-radius: min($calculator-radius-width, $calculator-radius-height);
    padding: min($button-gap-width, $button-gap-height)
      min($button-gap-width, $button-gap-height);
    border: $border;

    .screen-container {
      width: 100%;
      height: $button-height;
      padding-bottom: min($button-gap-width, $button-gap-height);

      button {
        width: 100%;
        height: 100%;
        font-family: calculatorFont;
        /* font-size: calc($button-font-size); */
        font-size: min($screen-font-size-width, $screen-font-size-height);
        font-weight: bolder;
        text-align: right;
        border-radius: min($button-radius-width, $button-radius-height);
        padding: 0
          min($screen-button-padding-width, $screen-button-padding-height);
        border: $border;
      }
    }

    .buttons {
      width: 100%;
      height: calc($button-height * 5);
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      grid-template-rows: repeat(5, 1fr);
      column-gap: min($button-gap-width, $button-gap-height);
      row-gap: min($button-gap-width, $button-gap-height);
      padding-top: min($button-gap-width, $button-gap-height);

      button {
        /* font-size: calc($button-font-size - 10px); */
        font-size: min($button-font-size-width, $button-font-size-height);
        border-radius: min($button-radius-width, $button-radius-height);
        border: $border;
      }

      .purple-background {
        background-color: $purple-background-color;
      }

      .green-background {
        background-color: $green-background-color;
      }

      .button-clear {
        background-color: $button-clear-background-color;
        border-color: $button-clear-border-color;
      }

      .button-clear:hover {
        background-color: $button-clear-hover-color;
        cursor: pointer;
      }

      .change-on-hover:hover {
        background-color: $button-color-hover-background;
        border-color: $button-color-hover-border;
        cursor: pointer;
      }
    }
  }
}
